<template>
  <div>
    <el-button @click="onClick(btn)" :key="btn.label" v-bind="btn.extra" v-for="btn in (btns || [])">{{btn.label}}</el-button>
  </div>
</template>

<script>
export default {
  props: {
    btns: {
      type: Array,
      required: true
    },
    scope: Object
  },
  methods: {
    async onClick (btn) {
      if (btn.handle) {
        try {
          await btn.handle(this.scope)
        } catch (e) {
          this.$handleError && this.$handleError(e, '')
        }
      }
    }
  }
}
</script>
